export interface MinimalHttpError {
  /** errno symbolic name** like `ENETUNREACH`, Not http status code. */
  code?: string | undefined;
  response?: {
    data?: {} | null;
    status?: number;
  };
  config?: {
    url?: string;
    method?: string;
    entityFqdn?: string;
    methodFqn?: string;
    artifactId?: string;
  };
  requestId: string | undefined;
}
